  -webkit-filter: opacity(0.8);
          filter: opacity(0.8);
}

.model{
  width: 100%;
  height: 100vh;
  position: fixed;
  top:0;
  left:0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 0, 0);
  transition:opacity .4s ease, visibility .4s ease, -webkit-transform .5s ease-in-out;
  transition:opacity .4s ease, visibility .4s ease, transform .5s ease-in-out;
  transition:opacity .4s ease, visibility .4s ease, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
  visibility: hidden;
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  overflow: hidden;
  z-index: 999;


}

.model.open{
  visibility: visible;
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);


}
.model img{
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 20px 0 20px;
  margin: 0 auto;



}

.model.open svg{
  position: fixed;
  top: 10px;
  right: 10px;
  width: 2rem;
  height: 2rem;
  padding: 5px;
  background-color: rgba(223, 241, 226,0.5);
  cursor: pointer;

}

